export default {
	data() {
		return {
			captchaImg: '',
			mobile:'',
			seconds: 120,
			inputcode:'',//用户输入的验证码
			code: '',//接口返回的验证码
			codeBtn: {
				text: '获取验证码',
				waitingCode: false,
				count: this.seconds
			},
			changeSuccess: false
		}
	},
	onLoad() {

	},
	methods: {
		sendCode: function () {
			let t = this;
			this.codeBtn.waitingCode = true;
			this.codeBtn.count = this.seconds;
			this.codeBtn.text = this.codeBtn.count + 's';
			
			let countdown = setInterval( () => {
				this.codeBtn.count--;
				this.codeBtn.text = this.codeBtn.count + 's';
				if( this.codeBtn.count < 0 ){
					clearInterval(countdown);
					this.codeBtn.text = '重新发送';
					this.codeBtn.waitingCode = false;
				}
			},1000);
			uni.request({
				url: this.apiServer + '/api/sms/sendmsg',
				method: 'POST',
				data: {
					mobile: this.mobile
				},
				success: res => {
					if (res.data.err == 0) {
						t.code = res.data.code;
						console.log(this.code);
						uni.showToast({
							title: '短信发送成功'
						});
					}
				}
			});
		},
		gotoLogin: function () {
			uni.navigateTo({
				url: 'login'
			})
		},
		forgetsubmit:function(){
			if(this.inputcode==this.code){
				if(this.passwd==''){
					uni.showToast({
						title: '密码不能为空'
					});
				}else{
					uni.request({
						url: this.apiServer+'/api/login/forgetPasswd',
						method: 'POST',
						data: {
							mobile:this.mobile,
							passwd:this.passwd
						},
						success: res => {
							if(res.data.err==0){
								uni.showToast({
									title: '更新成功'
								});
								this.changeSuccess = true;
							}else{
								uni.showToast({
									title: res.data.msg
								});
							}
						},
						fail: () => {},
						complete: () => {}
					});
				}
			}else{
				uni.showToast({
					title: '验证码输入错误'
				});
				return false;
			}
		}
	},
	computed: {
		disableCodeBtn: function() {
			return this.codeBtn.waitingCode || this.mobile.length < 11;
		}
	}
}
